$defaultFont: "Cairo";

@font-face {
    font-family: 'Menlo';
    font-style: normal;
    font-weight: normal;
    src: local('Menlo Regular'), url('../assets/fonts/Menlo/Menlo-Regular.woff') format('woff');
}

@import url("https://fonts.googleapis.com/css?family=Cairo:wght@200;300;400;600;700;900&Lato:wght@700&Nunito+Sans:200,300,400,700");
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
@import "./mixins/fonts.scss";

* {
    font-family: $defaultFont, sans-serif;
}

html {
    width: 100vw;
    overflow-x: hidden !important;
}

body {
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 5px;
        background-color: #f5f5f5;
        display: none !important;
    }

    &::-webkit-scrollbar {
        width: 12px;
        background-color: #f5f5f5;
        display: none !important;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        background-color: black;
    }
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}