.logoLoader {
    .logoLoaderBackground {
        width: 100vw;
        height: 100vh;
        flex: 1;
        position: fixed;
        top: 0px !important;
        display: flex;
        justify-content: center;
        background-color: white;
        //opacity: 0.5;
        z-index: 9998;


    }

    .loaderContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0px !important;
        z-index: 9999 !important;

        .logoCenter {
            padding: 0px !important;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100%;

            .spinner {
                width: 35rem !important;
                height: 50vh;
                animation-duration: 3s;
                border-width: 0px !important;
                user-select: none;
                //-webkit-user-drag: none;
                //--moz-user-drag: none;
                display: flex;
                justify-content: center;
                align-items: flex-end;
                padding-bottom: 30px;

                .loader {
                    //border: 1px solid rgba(0, 0, 0, 0.7);
                    width: 10rem;
                    height: auto;
                    object-fit: contain;
                }
            }

            .seperator {
                margin: 20px 0px;
                height: 1px;
                width: 100%;
                opacity: 0.4;
                background-color: rgba(0, 0, 0, 0.55);
            }

            .bottomLoader {
                background-color: black;
                height: 100%;
                margin-top: 0px;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                width: 100%;
                height: 50vh;
                z-index: 9999 !important;

                h1 {
                    margin-bottom: 50px !important;
                    padding-bottom: 0px !important;
                    text-align: center;
                    font-size: 1.1rem !important;
                    font-weight: 600 !important;
                    color: white !important;
                }


                .normalSpinner {
                    align-self: center;
                    justify-self: center;
                    margin-top: 0px;
                    width: 5rem;
                    height: 5rem;
                    border-width: 2.2px;
                }
            }
        }


    }


}