.global-loader {
    .loader-background {
        width: 100vw;
        height: 100vh;
        flex: 1;
        position: fixed;
        top: 0px !important;
        display: flex;
        justify-content: center;
        background-color: white;
        //opacity: 0.5;
        z-index: 9998;
    }

    .loader-container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0px !important;
        z-index: 9999 !important;

        .loader {
            width: 8rem;
            height: 8rem;
            border-width: 3px;
        }
    }
}
