@import "../../scss/constants/responsive.scss";

.course {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: black;
    width: 33.333333% !important;


    .url {
        .card {
            padding: 0px !important;
            display: inline-block !important;
            width: 100%;
            margin: 20px 0px;
            max-width: 400px;
            justify-content: center;
            display: flex;
            -webkit-box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
            -moz-box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
            box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
            white-space: normal;
            border: none;

            img {
                border-top-right-radius: 5px;
                border-top-left-radius: 5px;
                min-width: calc(100% + 0.5px);
                margin-top: 0px;
                margin-left: -0.5px;
                object-fit: contain;
                height: auto;

                @media screen and (min-width: 992px) {
                    //min-height: 200px;
                    //min-height: 224px;
                }
            }

            .body {
                overflow: hidden;
                text-overflow: ellipsis;
                padding: 15px !important;

                h1 {
                    //font-family: inherit;
                    font-family: sans-serif;
                    font-weight: bold;
                    //font-size: 21px;
                    font-size: 19px;
                    padding-right: 20px;
                    display: -webkit-box;
                    height: 54px;
                    //letter-spacing: -0.25px;
                    letter-spacing: -0.5px;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    line-height: 27px;
                }

                .author {
                    color: #6a6f73;
                    line-height: 13px;
                    font-size: 16px;
                    letter-spacing: -0.12px;
                    cursor: pointer !important;
                }

                p {
                    margin: 5px 0px 5px 0px;
                }

                .information {
                    color: #6a6f73;
                    font-size: 14px;
                    letter-spacing: -0.12px;
                }

                .price {
                    color: black !important;
                    width: 150px;
                    font-weight: bolder;
                    font-size: 20px;
                    margin-top: 5px;
                    margin-bottom: -5px;
                    letter-spacing: -0.3px;
                    display: inline-block;
                    margin: auto;
                }

                .registered {
                    text-align: end;
                    width: calc(100% - 150px);
                    display: inline-block;
                    opacity: 0.9;
                    letter-spacing: -0.25px !important;
                    font-weight: bold;
                    color: black !important;
                    font-size: 18px;
                    margin-top: 5px;
                    margin-bottom: -5px;
                }

                .rating {
                    display: inline;
                    font-weight: bold;
                    color: #e7a22d !important;
                    display: flex;
                    align-items: center;

                    .number {
                        margin-top: 3.1px;
                        display: flex;
                        align-items: center;
                    }

                    .stars {
                        margin-left: 5px;
                    }
                }

                .course-star {
                    margin-left: 1px;
                    font-size: 16px;
                    color: #e7a22d !important;
                }
            }

        }
    }
}