html {
    scroll-behavior: smooth;
}

nav.navbar {
    height: 172px !important;

    @media only screen and (max-width: 992px) {
        height: 100px !important;
    }


    //background-color: black;
    background-color:white;
    padding: 0px !important;
    z-index: 1200;

    box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
    -webkit-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
    -moz-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);

    .mourning-flag {
        height: 70px;
        width: 70px;
        position: absolute;
        left: 5px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;

        @media screen and (max-width: 992px) {
            left: 70px;
        }

        img {

            height: 50px;

            @media screen and (max-width: 992px) {
                height: 40px;
            }
        }
    }


    .turkish-flag {
        height: 70px;
        width: 70px;
        position: absolute;
        right: 30px;
        top: 30px;
        cursor: pointer;

        @media screen and (max-width: 992px) {
            right: 20px;
            top: 30px;
        }

        img {

            height: 50px;

            @media screen and (max-width: 992px) {
                height: 40px;
            }
        }
    }

    .navbar-top-container {
        height: 100px;
        align-items: center;
        justify-content: center;
        z-index: 99;
        background-color: transparent;

        @media screen and (min-width: 992px) {
            position: relative;
        }

        @media screen and (max-width: 992px) {
            background-color: white;
            box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
            -webkit-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
            -moz-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
        }

        .navbar-logo-container {
            padding: 0px;
            margin: 0px;
            margin: auto;
            cursor: pointer;

            .navbar-brand {
                font-family: inherit;
                color: black;
                font-weight: bolder;
                font-size: 37px;
                letter-spacing: -2px;
            }

            .logo {
                height: 75px;

                @media only screen and (max-width: 992px) {
                    height: 65px !important;
                }
            }
        }
    }

    .seperator {
        @media screen and (min-width: 992px) {
            //border-top: 0.5px solid lightgrey;
            border-top: black;
        }

        @media screen and (max-width: 992px) {
            border-top: 0.1px solid rgb(248, 248, 248);
        }

        z-index: 1200;
        width: 100%;
    }

    .navbar-bottom-container {
        height: 70px;
        flex-direction: row;
        z-index: 1200;

        @media only screen and (max-width: 992px) {
            display: none !important;
        }

        .navbar-bottom-flex {
            height: 100%;
            justify-content: center;
            align-items: center;
            display: flex;
            flex: 1;
            //color: white;
            color: black;


            &:hover {
                //color: white;
                //background-color: #05101a;
                background-color: black;
                //color: #50101a;
                //background-color: white;
                color: white;
                cursor: pointer;

                svg {
                    //color: #50101a !important;
                    color: white !important;
                }
            }

            .navbar-bottom-link {
                margin-left: 7.5px;
                text-align: center;
                color: inherit;
                font-size: 20px;
                letter-spacing: -0.32px;
                text-decoration: none;
            }
        }
    }

    .navbar-menu-toggler {
        position: absolute;
        width: 50px;
        height: 50px;
        cursor: pointer;
        left: 10px;

        .navbar-menu-toggler-icon-container {
            margin: auto;
            height: 25px;
            width: 25px;
            align-items: center;
            flex: 1;
        }

        @media only screen and (min-width: 992px) {
            display: none !important;
        }
    }

    .navbar-bottom-container-responsive {
        padding: 0px 0px;
        position: absolute;
        top: 100.5px;
        width: 100vw;
        background-color: white !important;
        z-index: 98 !important;
        //border-bottom: 1px solid lightgray;
        padding-bottom: 10px;
        box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);
        -webkit-box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);
        -moz-box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);

        @media only screen and (min-width: 992px) {
            display: none !important;
        }

        .navbar-bottom-responsive-flex {
            width: 100%;
            justify-content: center;
            align-items: center;
            height: 55px;
            display: flex;
            padding: 0px 20px;

            .navbar-bottom-responsive-link {
                margin: auto;
                margin-left: 20px;
                text-align: center;
                color: inherit;
                font-size: 20px;
                text-decoration: none;
                //color: white;
                //color: black;

                i {
                    //color: white !important;
                    color: black !important;
                }

                &:hover {
                    color: #999c9b;
                    //color: white;
                }
            }
        }
    }

    .navbar-fixed-wrapper {
        position: fixed;
        width: 100vw !important;
        z-index: 1200;
        height: 75px;
        top: 0px;
        left: 0px;
        box-shadow: -6px 7px 110px -28px rgba(0, 0, 0, 0.79);
        -webkit-box-shadow: -6px 7px 110px -28px rgba(0, 0, 0, 0.79);
        -moz-box-shadow: -6px 7px 110px -28px rgba(0, 0, 0, 0.79);
        visibility: hidden;

        @media screen and (max-width: 1200px) {
            display: none !important;
        }

        .navbar-bottom-container-fixed {
            height: 75px;
            flex-direction: row;
            position: fixed;
            left: 50%;
            top: 0px;
            transform: translateX(-50%);
            display: flex;

            .logo-container {


                .logo {
                    height: 55px;
                }
            }

            @media only screen and (max-width: 992px) {
                display: none !important;
            }

            .navbar-bottom-flex {
                height: 100%;
                justify-content: center;
                align-items: center;
                display: flex;
                flex: 1;
                color: black;


                &:nth-child(2) {
                    border-left: 1px solid lightgray;
                }

                &:not(:last-child) {
                    border-right: 1px solid lightgray;
                }

                &:last-child {
                    border-right: 1px solid lightgray;
                }

                &:not(:first-child) {
                    &:hover {
                        //color: white;
                        //color: #50101a;
                        //background-color: #05101a;
                        color: black;
                        //background-color: white;
                        cursor: pointer;
                    }
                }


                .navbar-bottom-link {
                    margin-left: 7.5px;
                    text-align: center;
                    color: inherit;
                    font-size: 20px;
                    letter-spacing: -0.32px;
                    text-decoration: none;
                }
            }
        }
    }

    .active {
        //color: white !important;
        background-color: gray;
        cursor: pointer;
        opacity: 1;
        color: white !important;

        svg {
            color: white !important;
        }
    }
}