.course {
    &:hover {
        .floatingCourse {
            display: flex !important;
        }

        .star {
            display: none !important;
        }
    }

    .star {
        -webkit-box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 0px 76px -30px rgba(0, 0, 0, 0.75);
        background-color: #FFDF00;
        position: fixed;
        right: 20px;
        width: 100px;
        height: 100px;
        bottom: 20px;
        z-index: 9999;
        border-radius: 50%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        border: 4px dashed black;
    }

    .floatingCourse {
        display: none;
        position: fixed;
        right: -40px;
        bottom: 0px;
        zoom: 0.8;
        z-index: 9999;

        &:hover {
            zoom: 0.92;
            transition: 3s ease-in-out;
        }
    }
}



.backgroundBlur {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 9998;
    background-color: rgba(0, 0, 0, 0.8);
}